<template>
    <main class="concord" id="concord-cards">
        <h2>Lessons</h2>
        <router-link to="in-call">
        <div v-bind:key="lesson.id" v-for="lesson in lessons">              
            <div class="card " data-color="" >            
                <section class="wrapper">
                    <img style="background-color: grey; background-image: url(https://iyogera.dev/iyogera2/img/business-analysis.png)" alt="">                
                    <footer class="card-footer">
                        <h5> {{lesson.topic}} </h5>
                        <h6> {{lesson.name}}</h6>
                    </footer>
                </section>                    
            </div>
        </div>
        </router-link>
    </main> 
</template>

<script>
import axios from 'axios';
//import TodoItem from './TodoItem.vue';
import Vue from 'vue'
import Buefy from 'buefy'
import 'buefy/dist/buefy.css'
import 'vue-material-design-icons/styles.css';
Vue.use(Buefy)

export default {
  name: "Lessons",
  
  data() {
    return {
      lessons: []
    }
  },
    
  created() {

    axios.get('https://iyogera.dev/iyogera2/api/live_lessons')

      .then(res => this.lessons = res.data)
      
      .catch(err => console.log(err));
    }
  
}
</script>

