    position: relative;
    display: block;
    overflow: auto;
    text-align: left;
}

.concord>header time {
    display: table-cell;
    float: left;
    color: black;
}

.concord>header time:first-child {
    color: #8f8f91;
}

.concord>header time h3 {
    color: black;
    font-weight: 700;
}

.concord>header avatar {
    display: table-cell;
    text-align: right;
    float: right;
    background-position: center;
    background-size: 120%;
    background-repeat: no-repeat;
    border-radius: 50%;
    height: 50px;
    width: 50px;
}

.items {
    width: 300px;
    color: #0B5AA2;
}

.items-head p {
    padding: 5px 20px;
    margin: 10px;
    color: black;
    font-weight: bold;
    font-size: 20px;
}

