    border: 1px solid;
    color: black;
}

.items-body {
    padding: 10px;
    margin: 10px;
    display: grid;
    grid-gap: 10px;
}

.items-body-content {
    padding: 10px;
    padding-right: 0px;
    display: grid;
    grid-template-columns: 10fr 1fr;
    font-size: 13px;
    grid-gap: 10px;
    border: 1px solid transparent;
    cursor: pointer;

}

.items-body-content i {
    align-self: center;
    font-size: 15px;
    color: #0B5AA2;
    font-weight: bold;
    animation: icon 1.5s infinite forwards;
}

@keyframes icon {

    0%,
    100% {
        transform: translate(0px);
    }

    50% {
        transform: translate(3px);
    }
}

.scroll {
    overflow: auto;
}

/* ================================= *
 * Card Styling + Default
 ==================================== */
.card {
    z-index: auto;
    cursor: pointer;
    display: inline-block;
    float: left;
    position: sticky;
    text-align: left;
    width: 89%;
    height: 250px;
    max-height: 250px;
    border-radius: 20px;
    margin: 15px 53px;
    overflow: hidden;
    background: white;
    box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.10),
        0px 1px 20px 0px rgba(0, 0, 0, 0.10);
    transform-origin: center;
    transition: transform, width, height, border-radius, top, left;
    transition-duration: 500ms;
    transition-timing-function: ease-in-out;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -o-user-select: none;
}

.card:hover {
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.card.gate_reserved {
    box-shadow: none;
}

.card .wrapper {
    height: 100%;
    width: 100%;
    position: relative;
    display: table;
}

.card h5,
.card h6,
.card p {
    margin: 4px 0;
}

.card h6,
.card p {
    color: #8f8f91;
}

.card h6 {
    text-transform: uppercase;
}

.card video,
.card img {
    display: table-cell;
    position: relative;
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.card>.wrapper>header,
.card>.wrapper>footer {
    display: table-cell;
    width: 100%;
    height: auto;
    padding: 15px 20px;
    box-sizing: border-box;
}

.card>.wrapper>footer {
    background: white;
    position: absolute;
    bottom: 0;
    left: 0;
}

.card:active {
    transform: scale(0.97);
}

/* Card Style: x */
.card.x hr {
    width: 90%;
    margin: 0px 30px;
    border: 1px solid;
    color: black;
}

.card.x h4,
.card.x p {
    color: white;
}

.card.x h6 {
    color: rgba(255, 255, 255, 0.65);
}

.card.x>.wrapper>header,
.card.x>.wrapper>footer {
    position: absolute;
    left: 0;
    background: transparent;
    padding: 15px 20px;
}

.card.x>.wrapper>header {
    top: 0;
}

.card.x>.wrapper>footer {
    bottom: 0;
}

/* Card Style: v */
.card.v .wrapper {
    display: block;
}

.card.v video {
    object-fit: cover;
}

.card.v video:before {
    position: absolute;
    right: 15px;
    top: 0;
    background: rgba(255, 255, 255, 0.3);
    content: "mute";
}

/* Card Style: i */
.card.i h2,
.card.i h6,
.card.i p {
    color: black;
}

.card.i h2 {
    width: 50%;
    text-transform: uppercase;
}

.card.i p {
    font-size: 0.7em;
}

.card.i>.wrapper>header,
.card.i>.wrapper>footer {
    position: absolute;
    left: 0;
    padding: 15px 20px;
}

.card.i>.wrapper>header {
    top: 0;
}

.card.i>.wrapper>header img {
    width: 70px;
    height: 70px;
    border-radius: 18px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    background-color: white;
}

.card.i>.wrapper>footer {
    background: transparent;
    bottom: 0;
}

button.card {
    position: relative;
    height: auto;
    width: auto;
    border: 0;
    outline: 0;
    font-weight: 600;
    color: rgba(28, 111, 215, 0.88);
    text-align: left;
    background: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 4.4px 13px;
}

/* Card Style~input styling*/
input[type="button"].concord {
    cursor: pointer;
    position: absolute;
    height: auto;
    width: auto;
    border: 0;
    outline: 0;
    font-weight: 600;
    color: rgba(28, 80, 245, 0.98);
    text-align: left;
    background: rgba(255, 255, 255, 0.95);
    border-radius: 20px;
    padding: 4.4px 13px;
    margin: 0;
    right: 20px;
    bottom: 19px;
    box-sizing: border-box;
}

input[type="button"].concord:active {
    color: rgba(28, 80, 245, 0.48);
}

input[type="button"].concord.exit {
    display: none;
    z-index: 99;
    top: 15px;
    right: 20px;
    bottom: initial;
    padding: 2px 6px;
    font-size: 1.2em;
    line-height: 1em;
    color: rgba(255, 255, 255, 0.75);
    background: rgba(20, 20, 25, 0.65);
}

input[type="button"].concord-exit:active {
    color: rgba(28, 80, 245, 0.48);
}

/* Card State: Content and Card active Styling*/
.card>.content {
    margin-top: 60px;
    margin-bottom: 20px;
    opacity: 0;
    width: 100%;
    height: 0px;
    transition: height, opacity, margin;
    transition-duration: 900ms;
    transition-timing-function: ease-in-out;
}

.card>.content p {
    margin: 35px 20px;
    color: #8f8f91;
}

.card>.content p+p {
    margin-top: 0;
}

.card>.content p>b {
    color: black;
}

.card>.content img {
    position: relative;
    display: block;
    width: 100%;
    height: 300px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.card.in-flight {
    cursor: default;
    z-index: 98;
    display: block;
    max-height: none;
    max-width: none;
    top: 40px !important;
    height: 95vh !important;
    width: 100vw !important;
    margin: 0;
    overflow-y: scroll;
    transform: none;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}

.card.active:hover {
    transform: none;
}

.card.in-flight>.wrapper {
    height: 55%;
}

.card.in-flight>input[type="button"].concord.exit {
    display: block;
}

.card.in-flight>.content {
    margin-top: 0px;
    height: 45%;
    background: white;
    opacity: 1;
}


@media screen and (min-width: 530px) {
    .card {
        width: 75%;
        margin: 15px 8px;
    }
}

@media screen and (min-width: 600px) {
    .card {
